import React from 'react';
import moment from 'moment';

const DateInfo = () => {
  const day = moment().format('dddd');
  const date = moment().format('YYYY-MM-DD');

  return (
    <div className="DateInfo">
      <div className="day">
        {day === 'Monday' && '월요일'}
        {day === 'Tuesday' && '화요일'}
        {day === 'Wednesday' && '수요일'}
        {day === 'Thursday' && '목요일'}
        {day === 'Friday' && '금요일'}
        {day === 'Saturday' && '토요일'}
        {day === 'Sunday' && '일요일'}
      </div>
      <div className="date">{date}</div>
    </div>
  );
};

export default DateInfo;



