import React, { Component } from 'react';
import Slider from 'rc-slider';
import 'rc-slider/assets/index.css';
import { useOutsider } from '../../contexts/outsider';

class EmptInfoList extends Component {
  state = {
    value: 0,
    place: ''
  };

  handleSliderChange = value => {
    this.setState({
      value: value
    });
  };

  handleNameChange = e => {
    this.setState({
      place: e.target.name
    });
  };

  handleSubmit = e => {
    e.preventDefault();
    this.state.value === 0 && alert('자리비움 시간을 입력해 주세요.');
    !this.state.place && alert('장소를 선택해 주세요.');
    if (this.state.value !== 0 && this.state.place) {
      this.props.onCreate(this.state);
    }
    this.setState({
      value: 0,
      place: ''
    });
  };

  render() {
    const marks = {
      10: '10분',
      20: '20분',
      30: '30분',
      40: '40분',
      50: '50분'
    };

    return (
      <form onSubmit={this.handleSubmit}>
        <h2>자리비움 설정</h2>
        <div className="SendForm">
          <div className="position">
            <span className="f">{this.state.value}</span>
            <span className="c">:</span>
            <span className="l">00</span>
          </div>
          <div className="time">
            <span className="poa">시 간</span>
            <Slider
              value={this.state.value}
              onChange={this.handleSliderChange}
              max={60}
              marks={marks}
            />
          </div>
          <div className="place">
            <span className="poa">장 소</span>
            <button
              type="button"
              name="화장실"
              onClick={this.handleNameChange}
              className={this.state.place === '화장실' ? 'select' : ''}
            >
              화장실
            </button>
            <button
              type="button"
              name="옥상"
              onClick={this.handleNameChange}
              className={this.state.place === '옥상' ? 'select' : ''}
            >
              옥상
            </button>
            <button
              type="button"
              name="회의실"
              onClick={this.handleNameChange}
              className={this.state.place === '회의실' ? 'select' : ''}
            >
              회의실
            </button>
          </div>
        </div>
        <button className="SendFormButton">자리비움</button>
      </form>
    );
  }
}

export default useOutsider(EmptInfoList);



